<template>
    <div>
      <div class="authentication" v-if="authentication">
        <span>企业认证</span>
        <div style="float: right">
          <el-button type="primary" size="small" @click="toAuthentication(1)">立即认证</el-button>
        </div>
      </div>

      <div class="verify" v-else>
         <div class="authentication-head">
           <div :class="current=='BaseForm'?'active':''" @click="totab(1)">1.基本信息</div>
           <div :class="current=='CompanyForm'?'active':''" @click="totab(2)">2.企业认证</div>
         </div>
        <div class="form">
          <keep-alive>
            <component :is="current" ></component>
          </keep-alive>
        </div>
      </div>
    </div>
</template>

<script>
  import BaseForm from "./BaseForm";
  import CompanyForm from "./CompanyForm";
    export default {
        name: "EnterpriseCertification",
        data(){
            return {
                current:'BaseForm',
                authentication:true,  //企业认证
            }
        },
        components:{
            BaseForm,
            CompanyForm
        },
        methods:{
            totab(i){
                if (i==1){
                  this.current = 'BaseForm';
                }
                if (i==2){
                    this.current = 'CompanyForm';
                }
            },
            toAuthentication(i){
                if (i==1){
                    this.authentication = false;
                }
                if (i==2){
                    this.authentication = true;
                }

            }
        }
    }
</script>

<style scoped lang="less">
.authentication {
  width: 621px;
  height: 121px;
  line-height: 121px;
  border: 1px solid rgba(121, 121, 121, 1);
  box-sizing: border-box;
  padding-left: 40px;
  padding-right: 68px;
  margin: 18px;
}
.verify {
  .authentication-head {
    height: 50px;
    text-align: center;
     div {
       display: inline-block;
       width: 120px;
       height: 50px;
       margin: 0 auto;
       font-size: 18px;
       font-weight: 650;
       cursor: pointer;
     }
    div:nth-child(1){

      color: #000;
      line-height: 50px;
      text-align: center;
      margin-right: 21.5px;
    }
    div:nth-child(2){
      color: #000;
      line-height: 50px;
      text-align: center;
      margin-left: 21.5px;
    }
  }
}
  .active {
    background-color: #4281ff;
    color: #FFFFFF!important;
  }
  .el-form-item__content{
    margin-left: 150px!important;
  }
  .form {
    padding: 21px 48px;
  }
</style>



