<template>
<div>
  <el-form ref="form" :model="form" label-width="80px">
    <el-form-item label="企业名称">
      <div style="width: 186px">
      <el-input v-model="form.name"></el-input>
      </div>
    </el-form-item>
    <el-form-item label="从事行业">
      <div style="width: 186px">
      <el-input v-model="form.name"></el-input>
      </div>
    </el-form-item>
    <el-form-item label="企业类型">
      <el-checkbox-group v-model="form.type">
        <el-checkbox label="高新技术企业" name="type"></el-checkbox>
        <el-checkbox label="技术先进性服务企业" name="type"></el-checkbox>
        <el-checkbox label="新产品备案企业" name="type"></el-checkbox>
        <el-checkbox label="其他" name="type"><el-input  placeholder="其他"></el-input>
        </el-checkbox>
      </el-checkbox-group>
    </el-form-item>
    <el-form-item label="从业人数">
      <div style="width: 186px">
      <el-input v-model="form.name"></el-input>
      </div>
    </el-form-item>
    <el-form-item label="注册时间">
      <el-col :span="11">
        <el-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
      </el-col>
    </el-form-item>
    <el-form-item label="注册资金(万元)">
      <div style="width: 186px">
        <el-input v-model="form.name"></el-input>
      </div>
    </el-form-item>
    <el-form-item label="*所属市县">
      <el-cascader
        size="large"
        :options="options"
        v-model="selectedOptions"
        @change="handleChange">
      </el-cascader>
    </el-form-item>



    <el-form-item label="企业简介">
      <el-input type="textarea" v-model="form.desc"   :autosize="{ minRows: 6, maxRows: 8}"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="onSubmit" size="middle">下一步</el-button>
      <el-button @click="close()">关闭</el-button>
    </el-form-item>
  </el-form>
</div>
</template>

<script>
    import { regionData,CodeToText } from 'element-china-area-data'
    export default {
        name: "BaseForm",
        data(){
            return {
                form: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    delivery: false,
                    type: [],
                    resource: '',
                    desc: '',
                },
                options: regionData,
                selectedOptions: []
            }
        },
        methods:{
            onSubmit() {
                console.log('submit!');
                this.$parent.totab(2);
            },
            handleChange (value) {
                var arr = [];
                value.forEach((item,index)=>{
                    arr.push(CodeToText[item])
                })
                this.form.city = arr;
                console.log(this.form.city)
            },
            close(){
                this.$parent.toAuthentication(2);
            }
        }
    }
</script>

<style scoped lang="less">

</style>



