<template>
    <div>
      <div class="center-header">
         <div class="header-content">
           <div class="header-content-l"><span>政企智慧服务平台</span>&nbsp;&nbsp;&nbsp;<span><</span>&nbsp;&nbsp;&nbsp;<span>企业需求征集系统</span></div>
           <div class="header-content-r"><span>欢迎您!王*德</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span>退出</span></div>
         </div>
         <div class="center-body">
              <div class="center-list">
                 <ul>
                   <li>企业中心</li>
                   <li :class="current=='EnterpriseCertification'?'active':''" @click="toTab(1)">企业认证</li>
                   <li :class="current=='TalentDemand'?'active':''" @click="toTab(2)" >人才需求</li>
                   <li :class="current=='IndustrialApplication'?'active':''" @click="toTab(3)" >工业项目申报</li>
                   <li :class="current=='IndustrialFinancing'?'active':''" @click="toTab(4)" >工业项目融资</li>
                   <li :class="current=='ScientificAchievements'?'active':''" @click="toTab(5)" >科技成果</li>
                   <li :class="current=='EnterpriseNeeds'?'active':''" @click="toTab(6)" >企业需求</li>
                   <li :class="current=='MessageNotification'?'active':''" @click="toTab(7)" >消息通知</li>
                 </ul>
              </div>
              <div class="center-info">
                  <div class="info-head">
                    <span>{{tabsName}}</span>
                    <div class="keyWords" v-show="keyword">
                      <span style="font-size: 14px;">关键词</span>
                      <div class="keyWords-input">
                        <el-input v-model="input" placeholder="请输入内容"></el-input>
                      </div>
                      <el-button size="small">查询</el-button>
                      <el-button type="primary" size="small" v-show="keyButton">立即发布</el-button>
                      <el-button type="primary" size="small" v-show="keyButton1">立即入驻</el-button>
                      <el-button type="primary" size="small" v-show="keyButton2">立即申报</el-button>
                    </div>
                  </div>
                  <div class="center-component">
                    <keep-alive></keep-alive>
                    <component :is="current"></component>
                  </div>
              </div>
         </div>
      </div>
    </div>
</template>

<script>
  import EnterpriseCertification from "./EnterpriseCertification";
  import EnterpriseNeeds from "./EnterpriseNeeds";
  import IndustrialApplication from "./IndustrialApplication";
  import IndustrialFinancing from "./IndustrialFinancing";
  import MessageNotification from "./MessageNotification";
  import ScientificAchievements from "./ScientificAchievements";
  import TalentDemand from "./TalentDemand";
    export default {
        name: "EnterpriseCenter",
        data(){
            return {
                current:'EnterpriseCertification',
                tabsName:'企业认证',
                input:'',
                keyButton:false,
                keyButton1:false,
                keyButton2:false,
                keyword:true
            }
        },
        components:{
            EnterpriseCertification,
            EnterpriseNeeds,
            IndustrialApplication,
            IndustrialFinancing,
            MessageNotification,
            ScientificAchievements,
            TalentDemand
        },
        methods:{
            toTab(i){
                if (i==1){
                    this.current = 'EnterpriseCertification';
                    this.tabsName = '企业认证';
                    this.keyword = false;
                }
                if (i==2){
                    this.current = 'TalentDemand';
                    this.tabsName = '人才需求';
                    this.keyword = true;
                    this.keyButton = true;
                    this.keyButton1 =false;
                    this.keyButton2 = false;
                }
                if (i==3){
                    this.current = 'IndustrialApplication';
                    this.tabsName = '工业项目申报';
                    this.keyword = true;
                    this.keyButton = false;
                    this.keyButton1 =false;
                    this.keyButton2 = true;
                }
                if (i==4){
                    this.current = 'IndustrialFinancing';
                    this.tabsName = '工业项目融资';
                    this.tabsName = '工业项目申报';
                    this.keyword = true;
                    this.keyButton = false;
                    this.keyButton1 =false;
                    this.keyButton2 = true;
                }
                if (i==5){
                    this.current = 'ScientificAchievements';
                    this.tabsName = '科技成果';
                    this.keyword = true;
                    this.keyButton = false;
                    this.keyButton1 = true;
                    this.keyButton2 = false;
                }
                if (i==6){
                    this.current = 'EnterpriseNeeds';
                    this.tabsName = '企业需求';
                    this.keyButton = true;
                    this.keyword = true;
                    this.keyButton = true;
                    this.keyButton1 = false;
                    this.keyButton2 = false;
                }
                if (i==7){
                    this.current = 'MessageNotification';
                    this.tabsName = '消息通知';
                    this.keyword = false;
                }
            },
        }
    }
</script>

<style scoped lang="less">
.center-header {
  width: 100%;
  height: 72px;
  background-color: #3c67c7;
  .header-content {
    width: 1201px;
    height: 72px;
    line-height: 72px;
    margin: 0 auto;
    .header-content-l {
        float: left;
        font-size: 18px;
        font-family: PingFangSC-Medium;
        font-weight: normal;
        font-stretch: normal;
        letter-spacing: 0px;
        color: #ffffff;
    }
    .header-content-r {
      float: right;
      font-family: PingFangSC-Regular;
      font-size: 14px;
      color: #ffffff;
    }
  }
}
  .center-body {
    height: 851px;
    width: 1201px;
    margin: 38px auto;
    .center-list {
      width: 247px;
      height: 100%;
      display: inline-block;
      border: solid 1px #e4eaee;
      box-sizing: border-box;

      ul {
        list-style: none;
        width: 247px;
        box-sizing: border-box;
        li:nth-child(1) {
          height: 50px;
          background-color: #3c67c7;
          line-height: 50px;
          text-align: center;
          font-size: 18px;
          color: #FFFFFF;
        }
        li:nth-child(2){
          height: 39px;
          line-height: 39px;
          text-align: center;
          font-size: 16px;
          color: #5c6466;
          border: solid 1px #e4eaee;
          cursor: pointer;

        }
        li:nth-child(3){
          height: 39px;
          line-height: 39px;
          text-align: center;
          font-size: 16px;
          color: #5c6466;
          border: solid 1px #e4eaee;
          cursor: pointer;
        }
        li:nth-child(4){
          height: 39px;
          line-height: 39px;
          text-align: center;
          font-size: 16px;
          color: #5c6466;
          border: solid 1px #e4eaee;
          cursor: pointer;
        }
        li:nth-child(5){
          height: 39px;
          line-height: 39px;
          text-align: center;
          font-size: 16px;
          color: #5c6466;
          border: solid 1px #e4eaee;
          cursor: pointer;
        }
        li:nth-child(6){
          height: 39px;
          line-height: 39px;
          text-align: center;
          font-size: 16px;
          color: #5c6466;
          border: solid 1px #e4eaee;
          cursor: pointer;
        }
        li:nth-child(7){
          height: 39px;
          line-height: 39px;
          text-align: center;
          font-size: 16px;
          color: #5c6466;
          border: solid 1px #e4eaee;
          cursor: pointer;
        }
        li:nth-child(8){
          height: 39px;
          line-height: 39px;
          text-align: center;
          font-size: 16px;
          color: #5c6466;
          border: solid 1px #e4eaee;
          cursor: pointer;
        }
      }
    }
  }
  .center-info {
    display: inline-block;
    width: 904px;
    height: 851px;
    float: right;
    .info-head {
      display: inline-block;
      width: 100%;
      height: 42px;
      line-height: 42px;
      background: #f5f8fa;
      border: #e4eaee;
      font-family: PingFangSC-Semibold;
      font-size: 18px;
      font-weight: normal;
      font-stretch: normal;
      letter-spacing: 0px;
      color: #2e3233;
      box-sizing: border-box;
      padding-left: 23px;

    }
  }
  .active {
    color: #3c67c7!important;
    background-color: #f0f3fb;
  }
  .center-component {
    width: 904px;
    height: 790px;
    margin-top: 16px;
    border: solid 1px #e4eaee;
  }
  .keyWords div{
    display: inline-block;
  }
  .keyWords-input {
    width: 145px;
    display: inline-block;
  }
  .info-head {
    position: relative;
  }
.keyWords {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
</style>



