<template>
  <div>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
    <el-form-item label="我的身份" prop="resource">
      <el-radio-group v-model="ruleForm.resource">
        <el-radio label="我是法人"></el-radio>
        <el-radio label="我是代理人"></el-radio>
      </el-radio-group>
    </el-form-item>

    <el-form-item label="统一社会信用代码" prop="name">
      <div style="width: 186px">
        <el-input v-model="ruleForm.name"></el-input>
      </div>

    </el-form-item>

      <el-form-item label="法定代表人" prop="name">
        <div style="width: 186px">
        <el-input v-model="ruleForm.name"></el-input>
        </div>
      </el-form-item>
      <el-form-item label="身份证号码" prop="name">
        <div style="width: 186px">
          <el-input v-model="ruleForm.name"></el-input>
        </div>
      </el-form-item>
      <el-form-item label="附件" prop="name">
        <el-upload
          class="upload-demo"
          action="https://jsonplaceholder.typicode.com/posts/"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          multiple
          :limit="3"
          :on-exceed="handleExceed"
          :file-list="fileList">
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="手机号码" prop="name">
        <div style="width: 186px">
        <el-input v-model="ruleForm.name"></el-input>
        </div>
      </el-form-item>
      <el-form-item label="验证码" prop="name">
        <div style="width: 102px">
          <el-input v-model="ruleForm.name"></el-input>
        </div>

      </el-form-item>
      <el-form-item label="手机验证码" prop="name">
        <div style="width: 102px">
          <el-input v-model="ruleForm.name"></el-input>
        </div>

      </el-form-item>
      <el-form-item label="代理人姓名" prop="name">
        <div style="width: 186px">
        <el-input v-model="ruleForm.name"></el-input>
        </div>
      </el-form-item>
      <el-form-item label="身份证号码" prop="name">
        <div style="width: 186px">
        <el-input v-model="ruleForm.name"></el-input>
          </div>
      </el-form-item>
      <div>
        <el-button type="primary">认证</el-button>
        <el-button type="danger">重新认证</el-button>
        <el-button @click="close">关闭</el-button>
      </div>
  </el-form>
  </div>
</template>

<script>
    export default {
        name: "CompanyForm",
        data(){
            return {
                fileList: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}],
                ruleForm: {
                    name: '',
                    region: '',
                    date1: '',
                    date2: '',
                    delivery: false,
                    type: [],
                    resource: '',
                    desc: ''
                },
                rules: {
                    name: [
                        { required: true, message: '请输入活动名称', trigger: 'blur' },
                        { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                    ],
                    region: [
                        { required: true, message: '请选择活动区域', trigger: 'change' }
                    ],
                    date1: [
                        { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
                    ],
                    date2: [
                        { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
                    ],
                    type: [
                        { type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change' }
                    ],
                    resource: [
                        { required: true, message: '请选择活动资源', trigger: 'change' }
                    ],
                    desc: [
                        { required: true, message: '请填写活动形式', trigger: 'blur' }
                    ]
                }
            }
        },
        methods:{
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            //图片
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            handleExceed(files, fileList) {
                this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
            },
            beforeRemove(file, fileList) {
                return this.$confirm(`确定移除 ${ file.name }？`);
            },
            //关闭
            close(){
                this.$parent.toAuthentication(2);
            }
        }
    }
</script>

<style scoped lang="less">

</style>



